import Vue from 'vue'
import Dialog from './Index.vue'

const DialogBox = Vue.extend(Dialog)
const defalutOptions = {
  title: "",
  content: "",
  type: "alert", //'alert','confirm'
  confirmButtonText: "", // 
  confirmButtonColor: "#991200", // 
  cancelButtonText: "", // 
  cancelButtonColor: "#888", // 
  overlay: true, // 
  closeOnClickOverlay: false, // 
  confirmCallback: () => {}, // 
  cancelCallback: () => {}, // 
  beforeClose: null, //  done() 
  visible: false
}

let instance;
Dialog.install =  function (options) {
  if (options === undefined || options === null || options === '') {
    return
  }
  // 
  if (options && (typeof options === 'string' || typeof options === 'number')) {
    options = {
      'content': options
    }
  }
  // 
  let realOptions = Object.assign({},defalutOptions,options);
  instance = new DialogBox({
    data: realOptions
  }).$mount();

  document.body.appendChild(instance.$el);

  Vue.nextTick(() => {
    if(realOptions.title !== '' || realOptions.content !== ''){
      instance.visible = true;
      // body
      document.body.className = 'shy-overflow-hidden'
    }
  });

  return Dialog
}
// 
Dialog.close = ()=>{
  if(instance){
    instance.visible = false;
  }
}

export default Dialog
